const FormModel = {
  userId: null,    // 与用户关联id
  code: '',        // 企业组织机构代码
  name: '',        // 企业名称
  email: '',       // 邮箱
  phone: '',       // 固定电话
  mobile: '',      // 手机
  nature: null,      // 企业性质，字典
  scale: '',         // 企业规模
  industry: null,    // 企业行业，字典
  life: '',          // 企业年限
  region: null,      // 所在省份（地区）
  city: '',          // 所在城市
  address: ''        // 企业详细地址
}

export default FormModel



